import React, { useEffect, useState } from "react";
import { StyleSheet, View, FlatList } from "react-native";

import { ListItem, ListItemSeparator, ListMsg } from "../components/lists";
import colors from "../config/colors";
import Icon from "../components/Icon";
import routes from "../navigation/routes";
import useAuth from "../auth/useAuth";
function AccountScreen({ navigation }) {
  const { user, logOut } = useAuth();
  const [menuItems, setmenuItems] = useState([]);
  const [isMounted, setIsMounted] = useState(true);

  useEffect(() => {
    return () => setIsMounted(false);
  }, []);

  useEffect(() => {
    if (isMounted) {
      if (user.isadmin) {
        setmenuItems([
          {
            id: "3",
            title: "سفارشهای تحویل نشده",
            icon: {
              name: "format-list-bulleted",
              backgroundColor: colors.primary,
            },
            targetScreen: routes.NOTDELIVERORDERS,
          },
          {
            id: "4",
            title: "ثبت قیمت حراج محصولات",
            icon: {
              name: "format-list-bulleted",
              backgroundColor: colors.primary,
            },
            targetScreen: routes.SALEPRICE,
          },
          {
            id: "5",
            title: "اصلاح هزینه حمل",
            icon: {
              name: "format-list-bulleted",
              backgroundColor: colors.primary,
            },
            targetScreen: routes.POSTPRICE,
          },
          {
            id: "6",
            title: "پیامها",
            icon: {
              name: "email",
              backgroundColor: colors.secondary,
            },
            targetScreen: routes.MESSAGES,
          },
        ]);
      } else {
        setmenuItems([
          {
            id: "1",
            title: "سفارشهای من",
            icon: {
              name: "format-list-bulleted",
              backgroundColor: colors.primary,
            },
            targetScreen: routes.MYORDERS,
          },
          {
            id: "2",
            title: "پیامها",
            icon: {
              name: "email",
              backgroundColor: colors.secondary,
            },
            targetScreen: routes.MESSAGES,
          },
        ]);
      }
    }
  }, []);
  return (
    <View style={styles.screen}>
      <View style={styles.container}>
        <ListMsg
          msgDirection="row-reverse"
          title={user.email}
          subTitle={user.address}
          bkColor={colors.white}
        />
      </View>
      {user.userid !== "guest" && (
        <View style={styles.container}>
          <FlatList
            data={menuItems}
            keyExtractor={(menuItem) => menuItem.id}
            ItemSeparatorComponent={ListItemSeparator}
            renderItem={({ item }) => (
              <ListItem
                title={item.title}
                IconComponent={
                  <Icon
                    name={item.icon.name}
                    backgroundColor={item.icon.backgroundColor}
                  />
                }
                onPress={() => navigation.navigate(item.targetScreen, user)}
              />
            )}
          />
        </View>
      )}
      <ListItem
        title="خروج"
        IconComponent={<Icon name="logout" backgroundColor="#ffe66d" />}
        onPress={() => logOut()}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  screen: {
    backgroundColor: colors.light,
  },
  container: {
    marginVertical: 10,
  },
});

export default AccountScreen;
