import React, { useState } from "react";
import {
  StyleSheet,
  TouchableWithoutFeedback,
  Modal,
  Button,
  ScrollView,
  View,
} from "react-native";
import * as Yup from "yup";

import Screen from "../components/Screen";
import useAuth from "../auth/useAuth";
import {
  ErrorMessage,
  Form,
  FormField,
  FormSwitchdbl,
  FormSwitchsng,
  SubmitButton,
} from "../components/forms";
import Text from "../components/Text";
import srvapi from "../api/srvapi";
import ActivityIndicator from "../components/ActivityIndicator";

const validationSchema = Yup.object().shape({
  userid: Yup.string()
    .required("لطفا کد کاربری را وارد نمایید")
    .label("User Id"),
  username: Yup.string()
    .required("لطفا نام کاربری وارد شود")
    .label("User Name"),
  email: Yup.string()
    .required("لطفا ایمیل معتبر وارد شود")
    .email()
    .label("Email"),
  userpass: Yup.string()
    .required("رمز عبور حداقل ۴ کاراکتر باید باشد")
    .min(4)
    .label("Password"),
  address: Yup.string()
    .required("لطفا آدرس جهت ارسال سفارش را ثبت گردد")
    .label("Address"),
  postalcode: Yup.string()
    .required("لطفا کد پستی را ثبت نمایید")
    .label("Postalcode"),
  approve: Yup.bool()
    .oneOf([true], "لطفا شرایط را تایید نمایید")
    .label("Approve"),
});

function RegisterScreen() {
  const auth = useAuth();
  const [error, setError] = useState();
  const [actVisible, setActVisible] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const handleSubmit = async (userInfo) => {
    try {
      setActVisible(true);
      const result = await srvapi.post("/register", userInfo);
      setActVisible(false);
      auth.logIn(result.data.accessToken);
    } catch (err) {
      setActVisible(false);
      setError("بروز خطا در ثبت اطلاعات");
    }
  };

  return (
    <>
      <ActivityIndicator visible={actVisible} />
      <Screen style={styles.container} scroll={true}>
        <Form
          initialValues={{
            userid: "",
            username: "",
            email: "",
            userpass: "",
            address: "",
            postalcode: "",
            telphone: "",
            province: "T",
            notifyflag: false,
            approve: false,
          }}
          onSubmit={handleSubmit}
          validationSchema={validationSchema}
        >
          <ErrorMessage error={error} visible={error} />
          <FormField
            autoCorrect={false}
            icon="account"
            name="userid"
            placeholder="کد کاربری"
          />
          <FormField
            autoCorrect={false}
            icon="account"
            name="username"
            placeholder="نام کاربر"
          />
          <FormField
            autoCapitalize="none"
            autoCorrect={false}
            icon="email"
            keyboardType="email-address"
            name="email"
            placeholder="ایمیل"
            textContentType="emailAddress"
          />
          <FormField
            autoCapitalize="none"
            autoCorrect={false}
            icon="lock"
            name="userpass"
            placeholder="رمز عبور"
            secureTextEntry
            textContentType="password"
          />
          <FormField
            autoCorrect={false}
            //icon="account"
            name="address"
            placeholder="آدرس تحویل سفارش"
          />
          <FormField
            autoCorrect={false}
            //icon="account"
            name="postalcode"
            placeholder="کد پستی"
          />
          <FormField
            autoCorrect={false}
            //icon="account"
            name="telphone"
            placeholder="شماره تماس"
          />
          <FormSwitchdbl
            switchTitle={["تهران", "سایر شهرها"]}
            switchValues={{ trueValue: "T", falseValue: "O" }}
            name="province"
          />
          <FormSwitchsng name="notifyflag" switchTitle="ارسال پیام تخفیف" />
          <FormSwitchsng
            name="approve"
            switchTitle="شرایط خدمات کانژوک و حریم خصوصی را می پذیرم"
          />
          <TouchableWithoutFeedback onPress={() => setModalVisible(true)}>
            <Text style={styles.linktext}>متن حریم خصوصی</Text>
          </TouchableWithoutFeedback>
          <SubmitButton title="ذخیره" />
        </Form>
        <Modal visible={modalVisible} animationType="slide">
          <Button title="Close" onPress={() => setModalVisible(false)} />
          <ScrollView>
            <View>
              <Text style={styles.title} numberOfLines={1}>
                حریم خصوصی
              </Text>
              <Text style={styles.text}>
                کانژوک استودیو با تاکید بر احترامی که برای حریم شخصی کاربران
                قائل است، برای ثبت نام و ثبت سفارش اطلاعاتی را از کاربران
                درخواست می‌کند تا بتواند خدماتی امن و مطمئن را به کاربران ارائه
                دهد. برای پردازش و ارسال سفارش، اطلاعاتی مانند آدرس، شماره تلفن
                و ایمیل مورد نیاز است . یادآوری می‌شود آدرس محل تحویل , ایمیل و
                تلفنی که مشتری در هنگام ثبت نام و تکمیل فرایند سفارش ثبت می‌کند،
                تنها اطلاعات‌ رسمی و مورد تایید مشتری است و تمام مکاتبات و
                پاسخ‌های کانژوک استودیو از طریق آنها صورت می‌گیرد
              </Text>
              <Text style={styles.text}>
                بنابراین درج آدرس، ایمیل و شماره تماس توسط مشتری، به منزله مورد
                تایید بودن صحت آنها است و در صورتی که موارد فوق به صورت صحیح یا
                کامل درج نشده باشد، کانژوک استودیو جهت اطمینان از صحت و قطعیت
                ثبت سفارش می‌تواند از مشتری، اطلاعات تکمیلی و بیشتری درخواست
                کند.
              </Text>
              <Text style={styles.text}>
                مشتریان می‌توانند نام، آدرس و تلفن شخص دیگری را برای تحویل گرفتن
                سفارش وارد کنند و کانژوک استودیو تنها برای ارسال همان سفارش، از
                این اطلاعات استفاده خواهد کرد
              </Text>
              <Text style={styles.text}>
                اطلاعات اخذ شده و همچنین جست‌وجو و سفارشهای ثبت شده تنها در
                دسترس کانژوک استودیو و کاربر مربوطه میباشد و در اختیار شریک
                تجاری دیگری قرار نمی گیرد
              </Text>
              <Text style={styles.text}>
                کانژوک استودیو در صورت اعلان تمایل کاربر در فرم ثبت نام , در
                خصوص اطلاع‌رسانی رویدادها از طریق ایمیل و یا پیام داخل برنامه
                اقدام می نماید. مبدا ایمیل های ارسالی info@kanzhookstudio.ir و
                kanzhookstudio@gmail.com میباشد
              </Text>
              <Text style={styles.text}>
                تاکید میشود که حفظ و نگهداری رمز عبور و نام کاربری بر عهده
                کاربران است و لذا برای جلوگیری از هرگونه سواستفاده احتمالی،
                کاربران نباید این اطلاعات را برای شخص دیگری فاش کنند
              </Text>
              <Text style={styles.text}>
                مسئولیت هرگونه خسارت احتمالی که ناشی از عدم رعایت موارد
                فوق‌الذکر توسط کاربر باشد، با خود کاربر بوده وکاربر حق هر گونه
                ادعا علیه کانژوک استودیو را از خود سلب می‌کند. کانژوک استودیو
                هویت شخصی کاربران را محرمانه می‌داند و اطلاعات شخصی آنان را به
                هیچ شخص یا سازمان دیگری منتقل نمی‌کند، مگر اینکه با حکم مقام
                قضایی و یا طبق قوانین و مقررات رایج کشور موظف باشد در اختیار
                مراجع ذی‌صلاح قرار دهد.در این موارد هیچ گونه مسئولیت قانونی مبنی
                بر جبران خسارت برای کانژوک استودیو وجود ندارد و کاربران با اعلام
                رضایت خود در استفاده از خدمات برنامه ضمنا حق هرگونه اعتراض را از
                خود سلب نموده اند
              </Text>
              <Text style={styles.text}>
                کانژوک استودیو برای حفاظت و نگهداری اطلاعات و حریم شخصی کاربران
                همه توان خود را به کار می‌گیرد و امیدوار است که تجربه خریدی امن،
                راحت و خوشایند را برای همه کاربران فراهم آورد
              </Text>
              <Text style={styles.text}>
                جهت امنیت بیشتر با ورود کاربر یک توکن با طول عمر ۳ روز ایجاد
                میشود که بعد از منقضی شدن آن نیاز است تا کاربر مجدد وارد سیستم
                شود
              </Text>
              <Text style={styles.text}>
                در صورت وجود هرگونه سوال، لطفا به شرح زیر اقدام شود
              </Text>
              <Text style={styles.texts}>
                ایمیل به info@kanzhookstudio.ir و kanzhookstudio@gmail.com و یا
                ارسال پیام دایرکت به اینستاگرام @kanzhook_studio
              </Text>
            </View>
          </ScrollView>
        </Modal>
      </Screen>
    </>
  );
}

const styles = StyleSheet.create({
  container: {
    padding: 10,
  },
  linktext: {
    color: "#3133f1",
    marginHorizontal: 15,
    marginBottom: 5,
  },
  title: {
    padding: 5,
    fontWeight: "600",
  },
  text: {
    padding: 5,
    fontWeight: "300",
    textAlign: "justify",
    marginHorizontal: 5,
  },
  texts: {
    padding: 5,
    fontWeight: "400",
    marginHorizontal: 5,
    marginBottom: 10,
  },
});

export default RegisterScreen;
