import React from "react";
import {
  View,
  Image,
  StyleSheet,
  TouchableWithoutFeedback,
} from "react-native";

import Text from "./Text";
import colors from "../config/colors";

function Card({ title, subTitle, footer, remarks, imageUrl, onPress }) {
  return (
    <TouchableWithoutFeedback onPress={onPress}>
      <View style={styles.card}>
        <Image
          resizeMode="contain"
          style={styles.image}
          tint="light"
          // preview={{ uri: thumbnailUrl }}
          source={{
            uri: imageUrl,
          }}
        />
        <View style={styles.detailsContainer}>
          <Text style={styles.title} numberOfLines={1}>
            {title}
          </Text>
          <Text style={styles.subTitle} numberOfLines={2}>
            {subTitle}
          </Text>
          {footer && (
            <Text style={styles.footer} numberOfLines={2}>
              {footer}
            </Text>
          )}
          {remarks && (
            <Text style={styles.subTitle} numberOfLines={2}>
              {remarks}
            </Text>
          )}
        </View>
      </View>
    </TouchableWithoutFeedback>
  );
}

const styles = StyleSheet.create({
  card: {
    borderRadius: 15,
    backgroundColor: colors.white,
    marginBottom: 20,
    overflow: "hidden",
  },
  detailsContainer: {
    padding: 20,
  },
  image: {
    width: "100%",
    height: 300,
  },
  subTitle: {
    color: colors.black,
    fontWeight: "bold",
  },
  footer: {
    color: colors.danger,
    fontWeight: "bold",
  },

  title: {
    marginBottom: 7,
  },
});

export default Card;
