import React from "react";
import { useFormikContext } from "formik";

import SwitchD from "../Switchd";

function FormSwitchd({
  name,
  width,
  switchTitle,
  switchValues,
  ...otherProps
}) {
  const { setFieldValue, values } = useFormikContext();

  return (
    <>
      <SwitchD
        isEnabled={values[name] == switchValues.trueValue}
        onChangeSwitch={() =>
          setFieldValue(
            name,
            values[name] == switchValues.trueValue
              ? switchValues.falseValue
              : switchValues.trueValue
          )
        }
        value={values[name]}
        width={width}
        switchTitle={switchTitle}
        {...otherProps}
      />
    </>
  );
}

export default FormSwitchd;
