import React from "react";
import { FlatList, StyleSheet, View } from "react-native";

import Card from "../components/Card";
import Button from "../components/Button";
import colors from "../config/colors";
import settings from "../config/settings";
import { numAddCama } from "../services/numAddCama";

function MyOrderDetail({ navigation, route }) {
  const products = route.params.products;
  return (
    <>
      <View style={styles.screen}>
        <FlatList
          data={products}
          keyExtractor={(product) => product._id}
          renderItem={({ item }) => (
            <Card
              title={item.productId.prdname}
              subTitle={`قیمت ${numAddCama(item.productId.price)} تومان`}
              footer={
                item.productId.discount > 0
                  ? `تخفیف ${numAddCama(item.productId.discount)} تومان`
                  : null
              }
              remarks={
                item.productId.remarks != "" ? item.productId.remarks : null
              }
              imageUrl={`${settings.apiUrl}/images/${item.productId.image1}`}
            />
          )}
        />
        <Button title="بازگشت" onPress={() => navigation.goBack()} />
      </View>
    </>
  );
}

const styles = StyleSheet.create({
  screen: {
    padding: 20,
    backgroundColor: colors.light,
    flex: 1,
  },
});

export default MyOrderDetail;
