import React from "react";
import { createStackNavigator } from "@react-navigation/stack";
import AccountScreen from "../screens/AccountScreen";
import MessagesScreen from "../screens/MessagesScreen";
import MyOrderList from "../screens/MyOrderList";
import MyOrderDetail from "../screens/MyOrderDetail";
import NotDeliverOrder from "../screens/NotDeliverOrder";
import SalePriceScreen from "../screens/SalePriceScreen";
import PostPriceScreen from "../screens/PostPriceScreen";

const Stack = createStackNavigator();

const AccountNavigator = () => (
  <Stack.Navigator
    screenOptions={{ headerShown: false, presentation: "modal" }}
  >
    <Stack.Screen
      name="Account"
      component={AccountScreen}
      // options={{
      //   title: "حساب کاربری",
      // }}
      //options={{ headerTitle: (props) => <LogoTitle {...props} /> }}
    />
    <Stack.Screen name="Messages" component={MessagesScreen} />
    <Stack.Screen name="MyOrderList" component={MyOrderList} />
    <Stack.Screen name="MyOrderDetail" component={MyOrderDetail} />
    <Stack.Screen name="NotDeliverOrder" component={NotDeliverOrder} />
    <Stack.Screen name="SalePriceScreen" component={SalePriceScreen} />
    <Stack.Screen name="PostPriceScreen" component={PostPriceScreen} />
  </Stack.Navigator>
);

export default AccountNavigator;
