import React, { useEffect, useState } from "react";
import { StyleSheet, View } from "react-native";
import Text from "../components/Text";
import Accordion from "../components/Accordion";
import colors from "../config/colors";
import { getParams } from "../api/param";
import Snackbar from "../components/Snackbar";
import defaultStyles from "../config/styles";

function InfoScreen() {
  const [accordionItems, setAccordionItems] = useState([]);
  const [isMounted, setIsMounted] = useState(true);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  useEffect(() => {
    return () => setIsMounted(false);
  }, []);

  let dtl;
  const getParam = async () => {
    try {
      const result = await getParams({
        params: { paramgroup: "3" },
      });
      if (result) {
        dtl = { k: [], s: [] };
        for (const item of result.data) {
          dtl[item.paramvalue] = [
            ...dtl[item.paramvalue],
            //{ paramcode: item.paramcode, paramname: item.paramname },
            item.paramname,
          ];
        }
        if (isMounted) {
          setAccordionItems([
            {
              id: 0,
              title: "خرید حضوری از کانسپت استور",
              dtlShow: true,
              DtlComponent: dtlComponent(dtl.k),
            },
            {
              id: 1,
              title: "جدول سایز لباس های کانژوک",
              dtlShow: false,
              DtlComponent: dtlComponent(dtl.s),
            },
            {
              id: 2,
              title: "نحوه شست و شوی",
              dtlShow: false,
              DtlComponent: dtlComponent([
                "بهتره یکسری نکات رو رعایت کنید🥰",
                "دوستانی که از ما تیشرت خریداری می‌کنند برای طول عمر بیشتر کارها",
                ".با آب ۳۰درجه شسته بشه",
                ".بعد از چند بار شست و شو اتو شود ",
                "استفاده از رنگین تاژ هم توصیه میشود❣",
              ]),
            },
            {
              id: 3,
              title: "درباره محصولات",
              dtlShow: false,
              DtlComponent: dtlComponent([
                "همونطور که در جریان هستین",
                "محصولات ما کار دست هستند به همین علت هیچ کدوم از کارها مشابه هم نیستند و حتی رنگ ها ممکنه چند درجه متفاوت باشند و تمام کار ها تک هستند و امکان تولید کار مشابه برای ما وجود نداره در نهایت شما یک اثر هنری تک نسخه رو خریداری میکنید که فقط متعلق به شماست❣",
              ]),
            },
          ]);
        }
      }
    } catch (err) {
      setAccordionItems([]);
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در بازیابی اطلاعات",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    }
  };
  const dtlComponent = (ds) => (
    <>
      {ds.map((item) => (
        <Text key={item} style={styles.text}>
          {item}
        </Text>
      ))}
    </>
  );

  useEffect(() => {
    getParam();
    return () => {
      setAccordionItems([]);
    };
  }, []);
  const handleSelect = (id) => {
    setAccordionItems(
      accordionItems.map((row) => {
        if (row.id === id) {
          return { ...row, dtlShow: !row.dtlShow };
        } else {
          return { ...row, dtlShow: false };
        }
      })
    );
  };

  return (
    <View style={styles.screen}>
      <Accordion data={accordionItems} onPress={(id) => handleSelect(id)} />
      <Snackbar
        time={5000}
        open={snackbar.open}
        msg={snackbar.msg}
        backColor={snackbar.backColor}
        onPress={() => setSnackbar({ ...snackbar, open: false })}
      />
    </View>
  );
}
const styles = StyleSheet.create({
  screen: {
    flex: 1,
    padding: 10,
    backgroundColor: colors.light,
  },
  text: {
    padding: 5,
    flexDirection: "row-reverse",
    fontWeight: "400",
  },
});
export default InfoScreen;
