import Snackbar from "../components/Snackbar";
import Screen from "../components/Screen";
import {
  ErrorMessage,
  Form,
  FormField,
  SubmitButton,
} from "../components/forms";

import srvapi from "../api/srvapi";
import useAuth from "../auth/useAuth";
import defaultStyles from "../config/styles";

const validationSchema = Yup.object().shape({
  userid: Yup.string()
    .required("لطفا کد کاربری را وارد نمایید")
    .label("User Id"),
  userpass: Yup.string()
    .required("لطفا رمز عبور را وارد نمایید")
    .min(4)
    .label("Password"),
});

function LoginScreen(props) {
  const auth = useAuth();
  const [loginFailed, setLoginFailed] = useState(false);
  const [submiting, setSubmiting] = useState(false);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  const handleSubmit = async ({ userid, userpass }) => {
    try {
      setSubmiting(true);
      const result = await srvapi.post("/loginpwa", { userid, userpass });
      setLoginFailed(false);
      setSubmiting(false);
      auth.logIn(result.data.accessToken);
    } catch (err) {
      setSubmiting(false);
      setSnackbar({
        msg: "خطا ورود به برنامه",
        open: true,
        backColor: defaultStyles.colors.danger,
      });
      setLoginFailed(true);
    }
  };

  return (
    <Screen style={styles.container}>
      <Image style={styles.logo} source={require("../assets/kanzhook.jpg")} />

      <Form
        initialValues={{ userid: "", userpass: "" }}
        onSubmit={handleSubmit}
        validationSchema={validationSchema}
      >
        <ErrorMessage
          error="کدکاربری و یا رمز عبور اشتباه میباشد"
          visible={loginFailed}
        />
        <FormField
          autoCorrect={false}
          icon="account"
          name="userid"
          placeholder="کد کاربری"
        />
        <FormField
          autoCapitalize="none"
          autoCorrect={false}
          icon="lock"
          name="userpass"
          placeholder="رمز عبور"
          secureTextEntry
          textContentType="password"
        />
        {submiting ? (
          <ActivityIndicator visible={submiting} color="#777" size="large" />
        ) : (
          <SubmitButton title="Login" />
        )}
      </Form>
      <Snackbar
        time={5000}
        open={snackbar.open}
        msg={snackbar.msg}
        backColor={snackbar.backColor}
        onPress={() => setSnackbar({ ...snackbar, open: false })}
      />
    </Screen>
  );
}

const styles = StyleSheet.create({
  container: {
    padding: 10,
  },
  logo: {
    width: 200,
    height: 50,
    alignSelf: "center",
    marginTop: 50,
    marginBottom: 20,
  },
});

export default LoginScreen;
