import srvapi from "./srvapi";

const endpoint = "/params";

export const getParams = (options) => {
  return srvapi.get(endpoint, { ...options }).catch((err) => {
    return Promise.reject(err);
  });
};

export const setParams = (data, options) => {
  return srvapi.put(endpoint, data, { ...options }).catch((err) => {
    console.log(err);
  });
};

export default {
  getParams,
  setParams,
};
