import React, { useState, useEffect } from "react";
import {
  FlatList,
  StyleSheet,
  View,
  Modal,
  Button,
  Text,
  ActivityIndicator,
  TouchableOpacity,
} from "react-native";
import * as Yup from "yup";
import Snackbar from "../components/Snackbar";
import defaultStyles from "../config/styles";
import {
  ListMsg,
  ListItemDeleteAction,
  ListItemSeparator,
} from "../components/lists";
import srvapi from "../api/srvapi";
import {
  ErrorMessage,
  Form,
  FormField,
  SubmitButton,
} from "../components/forms";
import { gregorianToJalali } from "../services/jalaliDate";
import ButtonIcon from "../components/ButtonIcon";
import { MaterialCommunityIcons } from "@expo/vector-icons";

const msgValidationSchema = Yup.object().shape({
  title: Yup.string().required("لطفا عنوان را وارد نمایید").label("Title"),
  description: Yup.string()
    .required("لطفا متن پیام را وارد نمایید")
    .label("Description"),
  touser: Yup.string()
    .required("لطفا گیرنده پیام را وارد نمایید")
    .label("To User"),
});

function MessagesScreen({ route }) {
  const user = route.params;
  const [messages, setMessages] = useState([]);
  const [refreshing, setRefreshing] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const [error, setError] = useState(null);
  const [submiting, setSubmiting] = useState(false);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  const [isMounted, setIsMounted] = useState(true);
  useEffect(() => {
    return () => setIsMounted(false);
  }, []);

  const DateGtoJ = (pDate) => {
    const gDateAr = new Date(pDate).toISOString().substring(0, 10).split("-");
    return gregorianToJalali(gDateAr[0], gDateAr[1], gDateAr[2], true);
  };

  const handleSubmitMsg = async ({ title, description, touser }) => {
    try {
      setSubmiting(true);
      const result = await srvapi
        .post("/sendmsg", {
          title,
          description,
          touser,
          fromuser: user.userid,
        })
        .catch((err) => {
          throw err;
        });
      setMessages([...messages, result.data]);
      setModalVisible(false);
      setSubmiting(false);
    } catch (err) {
      setSubmiting(false);
      setSnackbar({
        msg: "خطا در ثبت پیام جدید",
        open: true,
        backColor: defaultStyles.colors.danger,
      });
      setError("خطا در ثبت اطلاعات");
    }
  };

  const getMessages = async () => {
    const result = await srvapi
      .get("/messages", {
        params: { userid: user.userid },
      })
      .catch((err) => {
        setMessages([]);
        if (err?.errcode === 403) {
          setSnackbar({
            msg: "لطفا مجدد وارد برنامه شوید",
            open: true,
            backColor: defaultStyles.colors.danger,
          });
        } else {
          setSnackbar({
            msg: "خطا در بازیابی اطلاعات",
            open: true,
            backColor: defaultStyles.colors.danger,
          });
        }
      });
    if (result && isMounted) setMessages(result?.data ?? []);
  };
  useEffect(() => {
    getMessages();
  }, []);

  const handleDelete = (message) => {
    setMessages(messages.filter((m) => m._id !== message._id));
  };
  const HeaderComponent = () => (
    <View style={{ flexDirection: "row-reverse" }}>
      <View />
      <ButtonIcon
        onPress={() => setModalVisible(true)}
        icnName="plus-circle"
        title="پیام جدید"
        style={styles.button}
      />
    </View>
  );
  return (
    <View style={{ flex: 1 }}>
      <Modal visible={modalVisible} animationType="slide">
        <View style={styles.container}>
          {/* <Button
            title="انصراف"
            onPress={() => {
              setModalVisible(false);
            }}
          /> */}
          <View style={{ flexDirection: "row" }}>
            <TouchableOpacity
              onPress={() => {
                setModalVisible(false);
              }}
            >
              <MaterialCommunityIcons
                name="close"
                size={20}
                style={{ paddingHorizontal: 10 }}
              />
            </TouchableOpacity>
            <Text
              style={{
                fontSize: 18,
                fontWeight: "bold",
                textAlign: "center",
                width: "80%",
              }}
            >
              ارسال پیام جدید
            </Text>
          </View>
          <Form
            initialValues={{
              title: "",
              description: "",
              touser: !user.isadmin ? "admin" : "",
            }}
            onSubmit={handleSubmitMsg}
            validationSchema={msgValidationSchema}
          >
            <ErrorMessage error={error} visible={error} />
            <FormField
              autoCorrect={false}
              name="title"
              placeholder="عنوان پیام"
            />
            <FormField
              autoCorrect={false}
              name="description"
              placeholder="متن پیام"
            />
            {user.isadmin && (
              <FormField
                autoCapitalize="none"
                autoCorrect={false}
                name="touser"
                placeholder="گیرنده پیام"
              />
            )}
            {submiting ? (
              <ActivityIndicator
                visible={submiting}
                color="#777"
                size="large"
              />
            ) : (
              <SubmitButton title="ارسال پیام" />
            )}
          </Form>
        </View>
      </Modal>
      <HeaderComponent />
      <FlatList
        data={messages}
        keyExtractor={(message) => message._id}
        // ListHeaderComponent={HeaderComponent}
        renderItem={({ item }) => (
          <ListMsg
            msgDirection={item.touser === user.userid ? "row" : "row-reverse"}
            bkColor={item.touser === user.userid ? "#aaa" : "#ddd"}
            header={`${item.fromuser}=>${item.touser},${DateGtoJ(
              item.updated
            )}`}
            title={item.title}
            subTitle={item.description}
            renderRightActions={() => (
              <ListItemDeleteAction onPress={() => handleDelete(item)} />
            )}
          />
        )}
        ItemSeparatorComponent={ListItemSeparator}
        refreshing={refreshing}
        onRefresh={() => getMessages()}
      />
      <Snackbar
        time={5000}
        open={snackbar.open}
        msg={snackbar.msg}
        backColor={snackbar.backColor}
        onPress={() => setSnackbar({ ...snackbar, open: false })}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: defaultStyles.colors.light,
    borderRadius: 25,
    padding: 15,
    marginVertical: 10,
  },
  button: {
    backgroundColor: defaultStyles.colors.primary,
    borderRadius: 10,
    justifyContent: "center",
    alignItems: "center",
    padding: 10,
    width: "40%",
    marginVertical: 10,
    marginHorizontal: 10,
    flexDirection: "row-reverse",
  },
});

export default MessagesScreen;
