import React, { useEffect, useState } from "react";
import {
  View,
  StyleSheet,
  Modal,
  TextInput,
  Linking,
  ActivityIndicator,
} from "react-native";
import * as Yup from "yup";
import {
  GestureHandlerRootView,
  Swipeable,
} from "react-native-gesture-handler";
import Snackbar from "../components/Snackbar";
import srvapi from "../api/srvapi";
import Card from "../components/Card";
import colors from "../config/colors";
import Screen from "../components/Screen";
import settings from "../config/settings";
import { numAddCama } from "../services/numAddCama";
import useAuth from "../auth/useAuth";
import Text from "../components/Text";
import Button from "../components/Button";
import {
  ErrorMessage,
  Form,
  FormField,
  FormSwitchmlt,
  SubmitButton,
} from "../components/forms";
import { ListItemDeleteAction } from "../components/lists";
import defaultStyles from "../config/styles";

const validationSchema = Yup.object().shape({
  address: Yup.string()
    .required("لطفا آدرس جهت ارسال سفارش را ثبت نمایید")
    .label("Address"),
  postalcode: Yup.string()
    .required("لطفا کد پستی را ثبت نمایید")
    .label("Postalcode"),
  telphone: Yup.string()
    .required("لطفا تلفن تماس را ثبت نمایید")
    .label("Telphone"),
});

function CartOrderScreen() {
  const { user, cart, setCart, salePrices } = useAuth();
  const [cartAcc, setCartAcc] = useState({ totalPrice: 0, totalDiscount: 0 });
  const [error, setError] = useState();
  const [modalVisible, setModalVisible] = useState(false);
  const [submiting, setSubmiting] = useState(false);
  const [resDis, setresDis] = useState(undefined);
  const [isMounted, setIsMounted] = useState(true);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  //const [isSwiping, setIsSwiping] = useState(false);
  const [payTxn, setPayTxn] = useState({
    amount: 0,
    authority: "",
    refId: "",
    error: false,
    //reqCall: false,
    vwCall: false,
    discountcode: "",
  });
  const [deliveryCost, setDeliveryCost] = useState([
    {
      type: "M",
      cost: 0,
      title:
        "ارسال با پیک موتوری , هزینه حمل در زمان تحویل توسط مشتری پرداخت میشود",
    },
    { type: "T", cost: 12000, title: "ارسال پست تهران" },
    { type: "O", cost: 17000, title: "ارسال پست به سایر شهرها" },
  ]);
  const [orderInfo, setOrderInfo] = useState({
    address: user.address,
    postalcode: user.postalcode,
    telphone: user.telphone,
    delivery: user.province == "T" ? 0 : 2,
  });

  //   const eventSource = new RNEventSource("http://172.21.0.235:5000/cbpayreq");
  //   useEffect(() => {
  //     console.log("ev");
  //     eventSource.addEventListener("message", (event) => {
  //       console.log(event); // message
  //     });

  //     //eventSource.onmessage = (e) => console.log(JSON.parse(e.data));
  //     return () => {
  //       eventSource.removeAllListeners();
  //       eventSource.close();
  //     };
  //   }, []);
  useEffect(() => {
    return () => setIsMounted(false);
  }, []);

  const accItem = (prdcategory, discount, price, totalPrice, totalDiscount) => {
    let saleValue = 0;
    const saleParam = salePrices.find(
      ({ paramcode }) => paramcode == prdcategory
    );
    if (saleParam) saleValue = saleParam.paramvalue;
    if (saleValue > 0) {
      return { totalPrice: totalPrice + ~~saleValue, totalDiscount };
    } else {
      return {
        totalPrice: totalPrice + price,
        totalDiscount: totalDiscount + discount,
      };
    }
  };

  const saleDiscount = (prdcategory, discount) => {
    let saleValue = 0;
    const saleParam = salePrices.find(
      ({ paramcode }) => paramcode == prdcategory
    );
    if (saleParam) saleValue = saleParam.paramvalue;
    if (saleValue > 0) {
      return `قیمت حراج ${numAddCama(saleValue)} تومان`;
    } else {
      return discount > 0 ? `تخفیف ${numAddCama(discount)} تومان` : null;
    }
  };

  const handleSubmit = async (purchaseInfo) => {
    setOrderInfo({
      ...purchaseInfo,
      delivery: deliveryCost.findIndex(
        (item) => item.type === purchaseInfo.delivery_type
      ),
    });
    setPayTxn((curState) => ({
      ...curState,
      amount:
        cartAcc.totalPrice +
        ~~deliveryCost.find((item) => item.type === purchaseInfo.delivery_type)
          .cost -
        cartAcc.totalDiscount -
        (resDis?.discountcode === payTxn.discountcode
          ? resDis.discountamount
          : 0),
    }));
    setModalVisible(true);
  };
  const handleDelete = (item) => {
    setCart(cart.filter((p) => p._id !== item._id));
  };

  // const handlePayDtl = async () => {
  //   try {
  //     setSubmiting(true);
  //     const result = await srvapi.get("/PaymentVerification", {
  //       params: {
  //         amount: payTxn.amount,
  //         token: payTxn.authority,
  //       },
  //     });
  //     if (result.data.status == 100 || result.data.status == 101) {
  //       setPayTxn((curState) => ({
  //         ...curState,
  //         refId: result.data.RefID,
  //         error: false,
  //         vwCall: true,
  //       }));
  //       await srvapi.post("/order", {
  //         ...(user.userid === "guest"
  //           ? { buyer_type: 1, buyer_name: "کاربر مهمان" }
  //           : {
  //               buyer_type: 0,
  //               buyer: { userId: user._id },
  //               buyer_name: user.username,
  //             }),
  //         address: orderInfo.address,
  //         postalcode: orderInfo.postalcode,
  //         telphone: orderInfo.telphone,
  //         province: deliveryCost[orderInfo.delivery].type,
  //         products: cart.map((item) => ({ productId: item._id })),
  //         totalPrice: cartAcc.totalPrice,
  //         totalDiscount: cartAcc.totalDiscount,
  //         deliveryCost: deliveryCost[orderInfo.delivery].cost,
  //         delivery_type: deliveryCost[orderInfo.delivery].type,
  //         banktx: result.data.RefID,
  //       });
  //     } else {
  //       setPayTxn((curState) => ({
  //         ...curState,
  //         refId: "",
  //         error: true,
  //         vwCall: true,
  //       }));
  //     }
  //     setSubmiting(false);
  //   } catch (err) {
  //     setSubmiting(false);
  //     setError("خطا در ثبت اطلاعات");
  //     setPayTxn((curState) => ({
  //       ...curState,
  //       refId: "",
  //       error: true,
  //       vwCall: true,
  //     }));
  //     setSnackbar({
  //       msg: "خطا در بازیابی وضعیت پرداخت",
  //       open: true,
  //       backColor: defaultStyles.colors.danger,
  //     });
  //   }
  // };
  const handlePayment = async () => {
    try {
      setSubmiting(true);
      const result = await srvapi.get("/PaymentRequest", {
        params: {
          amount: payTxn.amount,
          desc: "خرید از کانژوک",
          email: user.email,
        },
      });
      //      const supported = await Linking.canOpenURL(result.data.url);
      await srvapi.post("/order", {
        ...(user.userid === "guest"
          ? { buyer_type: 1, buyer_name: "کاربر مهمان" }
          : {
              buyer_type: 0,
              buyer: { userId: user._id },
              buyer_name: user.username,
            }),
        address: orderInfo.address,
        postalcode: orderInfo.postalcode,
        telphone: orderInfo.telphone,
        province: deliveryCost[orderInfo.delivery].type,
        products: cart.map((item) => ({ productId: item._id })),
        totalPrice: cartAcc.totalPrice,
        totalDiscount: cartAcc.totalDiscount,
        deliveryCost: deliveryCost[orderInfo.delivery].cost,
        delivery_type: deliveryCost[orderInfo.delivery].type,
        discountcode:
          payTxn.discountcode === resDis?.discountcode
            ? resDis.discountcode
            : "",
        discountamount:
          payTxn.discountcode === resDis?.discountcode
            ? resDis.discountamount
            : 0,

        authority: result.data.authority,
      });
      await Linking.openURL(result.data.url);
      // setPayTxn((curState) => ({
      //   ...curState,
      //   reqCall: true,
      //   authority: result.data.authority,
      // }));
      setSubmiting(false);
      setModalVisible(false);
      setCart([]);
    } catch (err) {
      setSubmiting(false);
      setModalVisible(false);
      setError("خطا در ثبت اطلاعات");
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در عملیات پرداخت",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    }
  };
  const getParams = async () => {
    try {
      const result = await srvapi.get("/params", {
        params: { paramgroup: "1" },
      });
      if (result && isMounted) {
        setDeliveryCost(
          result.data.map((item) => ({
            type: item.paramcode,
            cost: item.paramvalue,
            title: item.paramname,
          }))
        );
      }
    } catch (err) {
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در بازیابی اطلاعات",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    }
  };
  const getUserdiscount = async () => {
    try {
      if (user.userid !== "guest") {
        const result = await srvapi.get("/userdiscount", {
          params: { _id: user._id },
        });
        if (result && isMounted) {
          if (
            new Date(result?.data?.discountexpdate) > new Date() &&
            result.data.discountamount > 0
          ) {
            setresDis(result.data);
          } else {
            setresDis(undefined);
          }
        }
      } else {
        setresDis(undefined);
      }
    } catch (err) {}
  };
  useEffect(() => {
    getParams();
    getUserdiscount();
  }, []);

  useEffect(() => {
    // setCartAcc(
    //   cart.reduce(
    //     (acc, item) =>
    //       (acc = {
    //         totalPrice: acc.totalPrice + item.price,
    //         totalDiscount: acc.totalDiscount + item.discount,
    //       }),
    //     { totalPrice: 0, totalDiscount: 0 }
    //   )
    // );
    if (isMounted) {
      setCartAcc(
        cart.reduce(
          (acc, item) =>
            (acc = accItem(
              item.prdcategory,
              item.discount,
              item.price,
              acc.totalPrice,
              acc.totalDiscount
            )),
          { totalPrice: 0, totalDiscount: 0 }
        )
      );
    }
  }, [cart]);
  const PaymentComponent = () => (
    <Modal visible={modalVisible} animationType="slide">
      <View style={styles.container}>
        {/* <Icon name="close" backgroundColor="#fff" iconColor="#333" /> */}

        <View style={styles.detailsContainer}>
          <Text style={styles.title} numberOfLines={1}>
            {` تعداد اقلام سبد ${cart.length}`}
          </Text>
          <Text style={styles.title} numberOfLines={1}>
            {` جمع مبلغ خرید ${numAddCama(cartAcc.totalPrice)} تومان`}
          </Text>
          <Text style={styles.title} numberOfLines={1}>
            {` جمع تخفیف ${numAddCama(
              cartAcc.totalDiscount +
                ~~(resDis?.discountcode === payTxn.discountcode
                  ? resDis.discountamount
                  : 0)
            )} تومان`}
          </Text>
          <Text style={styles.title} numberOfLines={1}>
            {`هزینه حمل ${numAddCama(
              deliveryCost[orderInfo.delivery].cost
            )} تومان`}
          </Text>
          <Text style={styles.title} numberOfLines={2}>
            {` ${deliveryCost[orderInfo.delivery].title}`}
          </Text>
          <Text style={styles.subTitle} numberOfLines={1}>
            {`مبلغ قابل پرداخت ${numAddCama(payTxn.amount)} تومان`}
          </Text>
          {/* {!payTxn.reqCall && ( */}
          <View
            style={{
              width: "100%",
              flexDirection: "row-reverse",
              marginTop: 10,
              justifyContent: "center",
            }}
          >
            <View
              style={{
                width: "40%",
                marginHorizontal: 10,
              }}
            >
              {submiting ? (
                <ActivityIndicator
                  visible={submiting}
                  color="#777"
                  size="large"
                />
              ) : (
                <Button title="پرداخت سفارش" onPress={handlePayment} />
              )}
            </View>
            <View
              style={{
                width: "40%",
                marginHorizontal: 10,
              }}
            >
              <Button
                title="انصراف"
                onPress={() => {
                  setModalVisible(false);
                }}
              />
            </View>
          </View>
          {/* )} */}
          {/* {payTxn.reqCall && (
            <View
              style={{
                width: "100%",
                flexDirection: "row-reverse",
                marginTop: 10,
                justifyContent: "center",
              }}
            >
              {!payTxn.vwCall && (
                <View
                  style={{
                    width: "80%",
                    marginHorizontal: 10,
                  }}
                >
                  {submiting ? (
                    <ActivityIndicator
                      visible={submiting}
                      color="#777"
                      size="large"
                    />
                  ) : (
                    <Button title="رویت وضعیت پرداخت" onPress={handlePayDtl} />
                  )}
                </View>
              )}
              {payTxn.vwCall && (
                <>
                  <View
                    style={{
                      width: "40%",
                      marginHorizontal: 10,
                    }}
                  >
                    <Button
                      title="بازگشت"
                      onPress={() => {
                        //                            navigation.navigate(routes.MYORDERS, user);
                        setModalVisible(false);
                        setCart([]);
                      }}
                    />
                  </View>
                  <View
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      width: "50%",
                      marginHorizontal: 10,
                    }}
                  >
                    <Text>
                      {payTxn.error
                        ? `خطا در عملیات پرداخت`
                        : `شماره پیگیری ${payTxn.refId}`}
                    </Text>
                  </View>
                </>
              )} 
            </View>
          )}*/}
        </View>
      </View>
    </Modal>
  );
  return (
    <>
      <Screen style={styles.screen} scroll={true}>
        <PaymentComponent />
        {cart.map((item) => (
          <View style={{ flex: 1 }} key={item._id}>
            <GestureHandlerRootView>
              <Swipeable
                renderRightActions={() => (
                  <ListItemDeleteAction onPress={() => handleDelete(item)} />
                )}
                // onSwipeStart={() => setIsSwiping({ isSwiping: true })}
                // onSwipeRelease={() => setIsSwiping({ isSwiping: false })}
              >
                <View style={{ flex: 1 }}>
                  <Card
                    title={item.prdname}
                    subTitle={`قیمت ${numAddCama(item.price)} تومان`}
                    footer={saleDiscount(item.prdcategory, item.discount)}
                    remarks={item.remarks != "" ? item.remarks : null}
                    imageUrl={`${settings.apiUrl}/images/${item.image1}`}
                  />
                </View>
              </Swipeable>
            </GestureHandlerRootView>
          </View>
        ))}
        <View style={styles.detailsContainer}>
          <Text style={styles.title} numberOfLines={1}>
            {` تعداد اقلام سبد ${cart.length}`}
          </Text>
          <Text style={styles.title} numberOfLines={1}>
            {` جمع مبلغ خرید ${numAddCama(cartAcc.totalPrice)} تومان`}
          </Text>
          <Text style={styles.footer} numberOfLines={1}>
            {` جمع تخفیف ${numAddCama(cartAcc.totalDiscount)} تومان`}
          </Text>
          <Form
            initialValues={{
              address: user.address,
              postalcode: user.postalcode,
              telphone: user.telphone,
              delivery_type: user.province == "T" ? "M" : "O",
              discountcode: "",
            }}
            onSubmit={handleSubmit}
            validationSchema={validationSchema}
          >
            <ErrorMessage error={error} visible={error} />
            <FormField
              autoCorrect={false}
              //icon="account"
              name="address"
              placeholder="آدرس تحویل سفارش"
            />
            <FormField
              autoCorrect={false}
              name="postalcode"
              placeholder="کد پستی"
            />
            <FormField
              autoCorrect={false}
              name="telphone"
              placeholder="شماره تماس"
            />
            {resDis && (
              <View>
                <View>
                  <TextInput
                    style={[styles.textContainer]}
                    onChangeText={(text) => {
                      setPayTxn({ ...payTxn, ["discountcode"]: text });
                    }}
                    value={payTxn["discountcode"]}
                    placeholder=" کد تخفیف "
                  />
                </View>
                {resDis?.discountcode === payTxn.discountcode && (
                  <Text
                    style={styles.text}
                    numberOfLines={1}
                  >{` مبلغ کدتخفیف ${numAddCama(
                    resDis.discountamount
                  )} تومان`}</Text>
                )}
              </View>
            )}
            {/* {resDis && <><FormField
              autoCorrect={false}              
              name="discountcode"
              placeholder="کد تخفیف"
            />
            {resDis.discountcode===1 && <Text style={styles.title} numberOfLines={1}>
            {` مبلغ تخفیف ${numAddCama(resDis.discountamount)}`}
          </Text>}
          </>
            } */}

            <FormSwitchmlt
              switchItems={[
                { switchTitle: "پیک موتوری تهران", switchValue: "M" },
                { switchTitle: "پست تهران", switchValue: "T" },
                { switchTitle: "پست سایر شهرها", switchValue: "O" },
              ]}
              name="delivery_type"
            />
            {cart.length > 0 && <SubmitButton title="تکمیل فرایند خرید" />}
          </Form>
        </View>
        <Snackbar
          time={5000}
          open={snackbar.open}
          msg={snackbar.msg}
          backColor={snackbar.backColor}
          onPress={() => setSnackbar({ ...snackbar, open: false })}
        />
      </Screen>
    </>
  );
}

const styles = StyleSheet.create({
  screen: {
    flex: 1,
    padding: 20,
    backgroundColor: colors.light,
  },
  container: {
    flex: 1,
    backgroundColor: colors.light,
  },
  button: {
    justifyContent: "center",
    alignItems: "center",
    padding: 5,
    margin: 10,
  },
  detailsContainer: {
    flex: 1,
    padding: 20,
    backgroundColor: colors.white,
  },
  subTitle: {
    color: colors.black,
    fontWeight: "bold",
  },
  footer: {
    color: colors.danger,
    fontWeight: "bold",
  },
  title: {
    marginBottom: 7,
  },
});
export default CartOrderScreen;
