import React, { useEffect, useState } from "react";
import {
  View,
  FlatList,
  TouchableWithoutFeedback,
  StyleSheet,
  ActivityIndicator,
} from "react-native";
import Snackbar from "../components/Snackbar";
import ActivityIndicatorCmd from "../components/ActivityIndicator";
import Button from "../components/Button";
import colors from "../config/colors";
import { ListItemSeparator } from "../components/lists";
import routes from "../navigation/routes";
import Text from "../components/Text";
import useApi from "../hooks/useApi";
import srvapi from "../api/srvapi";
import { gregorianToJalali } from "../services/jalaliDate";
import { numAddCama } from "../services/numAddCama";
import ButtonIcon from "../components/ButtonIcon";
import defaultStyles from "../config/styles";

const deliveryType = { M: "پیک موتوری", T: "پست تهران", O: "پست شهرستان" };
function NotDeliverOrder({ navigation, route }) {
  const user = route.params;
  const [submiting, setSubmiting] = useState(false);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  const getOrdersApi = useApi(() =>
    srvapi.get("/orders", { params: { type: "N" } }).catch((err) => {
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در بازیابی اطلاعات",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    })
  );
  const DateGtoJ = (pDate) => {
    const gDateAr = new Date(pDate).toISOString().substring(0, 10).split("-");
    return gregorianToJalali(gDateAr[0], gDateAr[1], gDateAr[2], true);
  };
  useEffect(() => {
    getOrdersApi.request();
  }, []);

  return (
    <>
      <ActivityIndicatorCmd visible={getOrdersApi.loading} />
      <View style={styles.screen}>
        {getOrdersApi.error && (
          <>
            <Text>خطا در بازیابی لیست سفارشات</Text>
            <Button
              title="تلاش مجدد / ورود مجدد به سیستم"
              onPress={getOrdersApi.request}
            />
          </>
        )}
        {getOrdersApi?.data?.length === 0 && <Text>هیچ رکوردی وجود ندارد</Text>}
        <FlatList
          data={getOrdersApi.data}
          keyExtractor={(order) => order._id}
          ItemSeparatorComponent={() => (
            <ListItemSeparator backgroundColor={colors.medium} />
          )}
          renderItem={({ item }) => (
            <>
              <TouchableWithoutFeedback
                onPress={() => navigation.navigate(routes.ORDER_DETAILS, item)}
              >
                <View style={styles.card}>
                  <Text style={styles.subTitle} numberOfLines={1}>
                    {`تاریخ سفارش: ${DateGtoJ(item.updated)}`}
                  </Text>
                  {/* </View> */}
                  <Text style={styles.subTitle} numberOfLines={1}>
                    {`مبلغ پرداختی ${numAddCama(
                      item.totalPrice + item.totalDiscount + item.deliveryCost
                    )}`}
                  </Text>
                  {item.buyer_name && (
                    <Text style={styles.subTitle} numberOfLines={1}>
                      {`خریدار ${item.buyer_name}`}
                    </Text>
                  )}
                  <Text style={styles.title} numberOfLines={1}>
                    {`آدرس ${item.address}`}
                  </Text>
                  <Text style={styles.title} numberOfLines={1}>
                    {`کدپستی ${item.postalcode}`}
                  </Text>
                  <Text style={styles.title} numberOfLines={1}>
                    {`شماره پیگیری ${item.banktx ?? " نامشخص "}`}
                  </Text>
                  <Text style={styles.title} numberOfLines={1}>
                    {`نحوه تحویل ${deliveryType[item.delivery_type]}`}
                  </Text>
                </View>
              </TouchableWithoutFeedback>
              {submiting ? (
                <ActivityIndicator
                  visible={submiting}
                  color="#777"
                  size="large"
                />
              ) : (
                <ButtonIcon
                  enable={item.banktx !== ""}
                  onPress={async () => {
                    if (item.banktx !== "") {
                      try {
                        setSubmiting(true);
                        await srvapi.put("/deliverOrder", {
                          _id: item._id,
                        });
                        getOrdersApi.setData(
                          getOrdersApi.data.filter(
                            ({ _id }) => _id !== item._id
                          )
                        );
                        setSubmiting(false);
                        setSnackbar({
                          msg: "ثبت اطلاعات با موفقیت انجام شد",
                          open: true,
                          backColor: defaultStyles.colors.success,
                        });
                      } catch (err) {
                        setSubmiting(false);
                        setSnackbar({
                          msg: "خطا در به روز رسانی اطلاعات",
                          open: true,
                          backColor: defaultStyles.colors.danger,
                        });
                      }
                    }
                  }}
                  title="ارسال شد"
                  icnName="cube-send"
                />
              )}
            </>
          )}
        />
      </View>
      <Snackbar
        time={5000}
        open={snackbar.open}
        msg={snackbar.msg}
        backColor={snackbar.backColor}
        onPress={() => setSnackbar({ ...snackbar, open: false })}
      />
    </>
  );
}

const styles = StyleSheet.create({
  screen: {
    padding: 20,
    backgroundColor: colors.light,
    flex: 1,
  },
  card: {
    borderRadius: 15,
    backgroundColor: colors.white,
    marginBottom: 5,
  },
  subTitle: {
    color: colors.black,
    fontWeight: "bold",
  },
  title: {
    marginBottom: 7,
  },
});

export default NotDeliverOrder;
