import React from "react";
import { View, StyleSheet, TouchableHighlight, FlatList } from "react-native";
import { MaterialCommunityIcons } from "@expo/vector-icons";
import { ListItemSeparator } from "../components/lists";
import Text from "./Text";
import colors from "../config/colors";

function Accordion({ data, onPress }) {
  return (
    <FlatList
      data={data}
      keyExtractor={(item) => item.id}
      ItemSeparatorComponent={ListItemSeparator}
      renderItem={({ item }) => (
        <>
          <TouchableHighlight
            underlayColor={colors.light}
            onPress={() => onPress(item.id)}
          >
            <View style={[styles.container, { flexDirection: "row-reverse" }]}>
              <Text style={styles.title} numberOfLines={1}>
                {item.title}
              </Text>
              <MaterialCommunityIcons
                color={colors.medium}
                name={item.dtlShow ? "chevron-up" : "chevron-down"}
                size={25}
              />
            </View>
          </TouchableHighlight>
          {item.dtlShow && item.DtlComponent}
        </>
      )}
    />
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: "space-between",
    padding: 5,
    backgroundColor: colors.white,
    marginLeft: 5,
  },
  title: {
    fontWeight: "500",
  },
});

export default Accordion;
