apiInst.interceptors.request.use(
  async (config) => {
    const authToken = await authStorage.getToken();
    config.baseURL = settings.apiUrl;
    config.headers = authToken ? { "x-access-token": authToken } : {};
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);
apiInst.interceptors.response.use(
  (response) => response,
  async (error) => {
    if (error?.response?.status === 403) {
      return Promise.reject({ errcode: 403, msg: "Unautorized" });
    }
    return Promise.reject({ errcode: 500, msg: "unexeptedError" });
  }
);
