import React from "react";
import { View, Text, Switch, StyleSheet } from "react-native";

import defaultStyles from "../config/styles";

function AppSwitchd({
  width = "100%",
  switchTitle,
  isEnabled,
  onChangeSwitch,
}) {
  return (
    <View
      style={[styles.container, { width }, { flexDirection: "row-reverse" }]}
    >
      <Text style={styles.text}>{switchTitle[0]}</Text>
      <Switch onValueChange={onChangeSwitch} value={isEnabled} />
      <Text style={styles.text}>{switchTitle[1]}</Text>
      <Switch onValueChange={onChangeSwitch} value={!isEnabled} />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: defaultStyles.colors.light,
    borderRadius: 25,
    flexDirection: "row",
    padding: 15,
    marginVertical: 10,
  },
  text: {
    padding: 3,
  },
});

export default AppSwitchd;
