import React, { useEffect, useState } from "react";
import { FlatList, StyleSheet, View, TouchableHighlight } from "react-native";
import ActivityIndicator from "../components/ActivityIndicator";
import Button from "../components/Button";
import Card from "../components/Card";
import colors from "../config/colors";
import routes from "../navigation/routes";
import Text from "../components/Text";
import useApi from "../hooks/useApi";
import settings from "../config/settings";
import { numAddCama } from "../services/numAddCama";
import Picker from "../components/Picker";
import { getCategories } from "../services/datasets";
import CategoryPickerItem from "../components/CategoryPickerItem";
import { getProducts } from "../api/product";
import useAuth from "../auth/useAuth";
import { getParams } from "../api/param";
import defaultStyles from "../config/styles";
import Snackbar from "../components/Snackbar";
import { MaterialCommunityIcons } from "@expo/vector-icons";

const categories = getCategories();
function PrdListingsScreen({ navigation }) {
  const [category, setCategory] = useState(null);
  const [refreshing, setRefreshing] = useState(false);
  const [salePrices, setSalePrices] = useState([]);
  const [updparam, setUpdparam] = useState(false);
  //const { salePrices, setSalePrices } = useAuth();
  const { user } = useAuth();
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });

  const saleDiscount = (prdcategory, discount) => {
    let saleValue = 0;
    const saleParam = salePrices.find(
      ({ paramcode }) => paramcode == prdcategory
    );
    if (saleParam) saleValue = saleParam.paramvalue;
    if (saleValue > 0) {
      return `قیمت حراج ${numAddCama(saleValue)} تومان`;
    } else {
      return discount > 0 ? `تخفیف ${numAddCama(discount)} تومان` : null;
    }
  };

  const getProductsApi = useApi((params = {}) =>
    getProducts({ params: { ...params } })
  );
  const getUpdparam = async () => {
    try {
      const result = await getParams({
        params: { paramgroup: "u" },
      });
      setUpdparam(
        result?.data?.find(({ paramvalue }) => paramvalue == "1") ? true : false
      );
    } catch (err) {
      setUpdparam(false);
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در بازیابی اطلاعات",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    }
  };
  const getSale = async () => {
    try {
      const result = await getParams({
        params: { paramgroup: "2" },
      });
      setSalePrices(result?.data ?? []);
    } catch (err) {
      setSalePrices([]);
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در بازیابی اطلاعات",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    }
  };

  useEffect(() => {
    getUpdparam();
    getSale();
    getProductsApi.request({ type: "A", prdcategory: null });
    return () => {
      setSalePrices([]);
    };
  }, []);

  const HeaderComponent = () => (
    <View
      style={{ justifyContent: "space-between", flexDirection: "row-reverse" }}
    >
      <Picker
        items={categories}
        numberOfColumns={3}
        onSelectItem={(item) => {
          setCategory(item);
          getProductsApi.request({ type: "A", prdcategory: item.value });
        }}
        PickerItemComponent={CategoryPickerItem}
        placeholder="فیلتر گروه"
        selectedItem={category}
      />
      <TouchableHighlight
        underlayColor={colors.light}
        onPress={async () => {
          //              setRefreshing(true);
          await getProductsApi.request({ type: "A", prdcategory: null });
          //setRefreshing(false);
        }}
      >
        <MaterialCommunityIcons
          style={{ marginVertical: 1, padding: 5 }}
          color="black"
          name="refresh"
          size={25}
        />
      </TouchableHighlight>
    </View>
  );

  return (
    <>
      <ActivityIndicator visible={getProductsApi.loading} />
      <View style={styles.screen}>
        {getProductsApi.error && (
          <>
            <Text>خطا در بازیابی لیست محصولات</Text>
            <Button
              title="تلاش مجدد / ورود مجدد به سیستم"
              onPress={getProductsApi.request}
            />
          </>
        )}
        {!user.isadmin && updparam ? (
          <Text style={styles.updText}>
            مشتریان محترم , فروشگاه در حال به روز رسانی میباشد , از شکیبای شما
            عزیزان سپاسگزاریم
          </Text>
        ) : (
          <>
        <HeaderComponent />
        <FlatList
          data={getProductsApi.data}
          keyExtractor={(product) => product._id}
          // ListHeaderComponent={HeaderComponent}
          refreshing={refreshing}
          onRefresh={async () => {
            setRefreshing(true);
            await getSale();
            await getProductsApi.request({ type: "A", prdcategory: null });
            setRefreshing(false);
          }}
          // refreshControl={
          //   <RefreshControl
          //     refreshing={refreshing}
          //     onRefresh={async () => {
          //       setRefreshing(true);
          //       await getSale();
          //       await getProductsApi.request({ type: "A", prdcategory: null });
          //       await sleep(2000);
          //       setRefreshing(false);
          //     }}
          //   />
          // }
          renderItem={({ item }) => (
            <Card
              title={item.prdname}
              subTitle={`قیمت ${numAddCama(item.price)} تومان`}
              footer={saleDiscount(item.prdcategory, item.discount)}
              remarks={item.remarks != "" ? item.remarks : null}
              imageUrl={`${settings.apiUrl}/images/${item.image1}`}
              onPress={() => {
                navigation.navigate(routes.PRODUCT_DETAILS, {
                  ...item,
                  prdcategory: item.prdcategory
                    ? item.prdcategory.toString()
                    : null,
                });
              }}
              //thumbnailUrl={item.images[0].thumbnailUrl}
            />
          )}
        />
        </>)}
        <Snackbar
          time={5000}
          open={snackbar.open}
          msg={snackbar.msg}
          backColor={snackbar.backColor}
          onPress={() => setSnackbar({ ...snackbar, open: false })}
        />
      </View>
    </>
  );
}

const styles = StyleSheet.create({
  screen: {
    padding: 3,
    backgroundColor: colors.light,
    flex: 1,
  },
  header: {
    justifyContent: "space-between",
  },
});

export default PrdListingsScreen;
