import React from "react";
import { View, Text, Switch, StyleSheet } from "react-native";

import defaultStyles from "../config/styles";

function AppSwitchm({
  width = "100%",
  switchItems,
  value,
  onChangeSwitch,
  selectedValue,
}) {
  return (
    <View
      style={[styles.container, { width }, { flexDirection: "row-reverse" }]}
    >
      {switchItems.map((item) => (
        <View key={item.switchValue}>
          <Text style={styles.text}>{item.switchTitle}</Text>
          <Switch
            onValueChange={() => {
              onChangeSwitch(item.switchValue);
            }}
            value={item.switchValue == selectedValue}
          />
        </View>
      ))}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: defaultStyles.colors.light,
    borderRadius: 25,
    flexDirection: "row",
    padding: 15,
    marginVertical: 10,
  },
  text: {
    padding: 3,
  },
});

export default AppSwitchm;
