import React, { useRef } from "react";
import {
  View,
  StyleSheet,
  KeyboardAvoidingView,
  Platform,
  Image,
  ScrollView,
} from "react-native";

import colors from "../config/colors";
import Text from "../components/Text";
import { numAddCama } from "../services/numAddCama";
import settings from "../config/settings";
import useAuth from "../auth/useAuth";
import routes from "../navigation/routes";
import ButtonIcon from "../components/ButtonIcon";

function ProductDetailsScreen({ navigation, route }) {
  const productDtl = route.params;
  const scrollView = useRef();
  const { user, cart, setCart, salePrices } = useAuth();

  const saleDiscount = (prdcategory, discount) => {
    let saleValue = 0;
    const saleParam = salePrices.find(
      ({ paramcode }) => paramcode == prdcategory
    );
    if (saleParam) saleValue = saleParam.paramvalue;
    if (saleValue > 0) {
      return `قیمت حراج ${numAddCama(saleValue)} تومان`;
    } else {
      return discount > 0 ? `تخفیف ${numAddCama(discount)} تومان` : null;
    }
  };

  return (
    <KeyboardAvoidingView
      behavior="position"
      keyboardVerticalOffset={Platform.OS === "ios" ? 0 : 100}
    >
      <ScrollView
        ref={scrollView}
        horizontal
        onContentSizeChange={() => scrollView.current.scrollToEnd()}
      >
        <View style={[{ flexDirection: "row-reverse" }]}>
          <View style={styles.image}>
            <Image
              style={styles.image}
              tint="light"
              resizeMode="contain"
              source={{
                uri: `${settings.apiUrl}/images/${productDtl.image1}`,
              }}
            />
          </View>
          {productDtl.image2 != "" && (
            <View style={styles.image}>
              <Image
                style={styles.image}
                tint="light"
                resizeMode="contain"
                source={{
                  uri: `${settings.apiUrl}/images/${productDtl.image2}`,
                }}
              />
            </View>
          )}
          {productDtl.image3 != "" && (
            <View style={styles.image}>
              <Image
                style={styles.image}
                tint="light"
                resizeMode="contain"
                source={{
                  uri: `${settings.apiUrl}/images/${productDtl.image3}`,
                }}
              />
            </View>
          )}
        </View>
      </ScrollView>
      <View style={styles.detailsContainer}>
        <Text style={styles.title}>{productDtl.prdname}</Text>
        <Text style={styles.price}>{`قیمت ${numAddCama(
          productDtl.price
        )} تومان`}</Text>
        <Text style={styles.discount}>
          {saleDiscount(productDtl.prdcategory, productDtl.discount)}
        </Text>
        <Text style={styles.title}>{productDtl.remarks}</Text>
        {user.isadmin && (
          <ButtonIcon
            onPress={() => {
              navigation.navigate(routes.PRODUCT_EDIT, {
                editMode: true,
                values: {
                  ...productDtl,
                  images: [],
                },
              });
            }}
            icnName="square-edit-outline"
            title="اصلاح اطلاعات"
          />
        )}
        {!user.isadmin && (
          <ButtonIcon
            onPress={() => {
              if (!cart.find(({ _id }) => _id === productDtl._id))
                setCart([...cart, productDtl]);
              navigation.navigate(routes.PRODUCTS_LIST);
            }}
            icnName="cart-plus"
            title="افزودن به سبد خرید"
          />
        )}
      </View>
    </KeyboardAvoidingView>
  );
}

const styles = StyleSheet.create({
  detailsContainer: {
    padding: 10,
  },
  image: {
    width: 300,
    height: 300,
    marginLeft: 10,
  },
  price: {
    color: colors.black,
    fontWeight: "bold",
    fontSize: 20,
    marginVertical: 5,
  },
  discount: {
    color: colors.danger,
    fontWeight: "bold",
    fontSize: 20,
    marginVertical: 5,
  },
  title: {
    fontSize: 24,
    fontWeight: "500",
  },
});

export default ProductDetailsScreen;
