import React, { useState } from "react";
import {
  ActivityIndicator,
  ImageBackground,
  Linking,
  TouchableOpacity,
  StyleSheet,
  View,
  Image,
  Text,
} from "react-native";
import Button from "../components/Button";
import routes from "../navigation/routes";
import colors from "../config/colors";
import Snackbar from "../components/Snackbar";
import srvapi from "../api/srvapi";
import useAuth from "../auth/useAuth";
import defaultStyles from "../config/styles";

function WelcomeScreen({ navigation }) {
  const auth = useAuth();
  const [submiting, setSubmiting] = useState(false);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  const handleSubmit = async () => {
    try {
      const capvalue = ~~(Math.random() * 100000).toString();
      setSubmiting(true);
      const result = await srvapi.post("/guestuserpwa", { capvalue });
      setSubmiting(false);
      auth.logIn(result.data.accessToken);
    } catch (err) {
      setSnackbar({
        msg: "خطای ورود به برنامه",
        open: true,
        backColor: defaultStyles.colors.danger,
      });
      setSubmiting(false);
    }
  };

  return (
    <ImageBackground
      blurRadius={2}
      source={require("../assets/fashion.jpg")}
      style={styles.background}
    >
      <View style={styles.logoContainer}>
        <Image style={styles.logo} source={require("../assets/kanzhook.jpg")} />
        <Text style={styles.text}>با ما خاص بودن را تجربه کنید</Text>
        <Text style={styles.text}>@kanzhook_studio</Text>
        <TouchableOpacity
          onPress={() => Linking.openURL("https://kanzhookstudio.ir")}
        >
          <Text style={styles.text}>https://kanzhookstudio.ir</Text>
        </TouchableOpacity>
      </View>
      <View style={styles.buttonContainer}>
        <Button
          title="ورود"
          onPress={() => navigation.navigate(routes.LOGIN)}
        />
        <Button
          title="عضویت"
          color="medium"
          onPress={() => navigation.navigate(routes.REGISTER)}
        />
        {submiting ? (
          <ActivityIndicator visible={submiting} color="#777" size="large" />
        ) : (
          <Button
            title="ورود مهمان"
            // onPress={() => navigation.navigate(routes.GUESTLOGIN)}
            onPress={handleSubmit}
          />
        )}
        <Snackbar
          time={5000}
          open={snackbar.open}
          msg={snackbar.msg}
          backColor={snackbar.backColor}
          onPress={() => setSnackbar({ ...snackbar, open: false })}
        />
      </View>
    </ImageBackground>
  );
}
const styles = StyleSheet.create({
  background: {
    flex: 1,
    justifyContent: "flex-end",
    alignItems: "center",
  },
  buttonContainer: {
    padding: 20,
    width: "100%",
  },
  logo: {
    width: 200,
    height: 50,
    alignSelf: "center",
  },
  logoContainer: {
    position: "absolute",
    top: 70,
    alignItems: "center",
  },
  text: {
    color: "#333",
    fontSize: 20,
    fontWeight: "600",
    paddingVertical: 20,
  },
});
export default WelcomeScreen;
