import React, { useEffect, useState } from "react";

import {
  View,
  TextInput,
  StyleSheet,
  ScrollView,
  ActivityIndicator,
} from "react-native";
import Button from "../components/Button";
import Text from "../components/Text";
import colors from "../config/colors";
import { numAddCama } from "../services/numAddCama";
import { getParams, setParams } from "../api/param";
import defaultStyles from "../config/styles";
import Snackbar from "../components/Snackbar";

function PostPriceScreen({ navigation }) {
  const [paramItems, setparamItems] = useState([]);
  const [paramValues, setparamValues] = useState({});
  const [fldTouched, setfldTouched] = useState(false);
  const [submiting, setSubmiting] = useState(false);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });
  const getParam = async () => {
    try {
      const result = await getParams({
        params: { paramgroup: "1" },
      });
      if (result) {
        setparamItems(result.data);
        for (const postPrice of result.data) {
          setparamValues((curState) => ({
            ...curState,
            [postPrice.paramcode.toString()]: postPrice.paramvalue,
          }));
        }
      }
    } catch (err) {
      if (err?.errcode === 403) {
        setSnackbar({
          msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      } else {
        setSnackbar({
          msg: "خطا در بازیابی اطلاعات",
          open: true,
          backColor: defaultStyles.colors.danger,
        });
      }
    }
  };
  const handleSubmit = () => {
    try {
      setSubmiting(true);
      setParams(
        {
          paramgroup: "1",
          paramItems: paramItems,
          paramValues: paramValues,
        },
        {}
      );
      setSubmiting(false);
      setfldTouched(false);
      navigation.navigate("Account");
    } catch (err) {
      setSubmiting(false);
    }
  };
  useEffect(() => {
    getParam();
    return () => {
      setparamItems([]);
      setparamValues({});
    };
  }, []);

  return (
    <ScrollView style={styles.screen}>
      <Text style={{ fontWeight: "bold", marginBottom: 10 }}>
        لیست نحوه و هزینه حمل
      </Text>
      {paramItems.map((item) => (
        <View key={item.paramcode}>
          <View>
            <Text>‍‍{` هزینه حمل ${item.paramname}`}</Text>
            <TextInput
              style={[styles.textContainer]}
              onChangeText={(text) => {
                if (paramValues[item.paramcode] != text) {
                  setfldTouched(true);
                  setparamValues({ ...paramValues, [item.paramcode]: text });
                }
              }}
              value={paramValues[item.paramcode]}
              //placeholder={item.paramname}
            />
          </View>
          <Text style={styles.text} numberOfLines={1}>{` هزینه حمل ${numAddCama(
            paramValues[item.paramcode]
          )} تومان`}</Text>
        </View>
      ))}
      {submiting ? (
        <ActivityIndicator visible={submiting} color="#777" size="large" />
      ) : (
        <Button title="ذخیره" onPress={handleSubmit} enable={fldTouched} />
      )}
      <Snackbar
        time={5000}
        open={snackbar.open}
        msg={snackbar.msg}
        backColor={snackbar.backColor}
        onPress={() => setSnackbar({ ...snackbar, open: false })}
      />
    </ScrollView>
  );
}
const styles = StyleSheet.create({
  screen: {
    flex: 1,
    padding: 10,
    backgroundColor: colors.white,
  },
  container: {
    flex: 1,
    backgroundColor: colors.light,
  },
  textContainer: {
    backgroundColor: defaultStyles.colors.light,
    borderRadius: 15,
    padding: 5,
    marginVertical: 5,
    direction: "rtl",
  },
  text: {
    color: colors.danger,
    fontWeight: "bold",
    marginBottom: 10,
  },
});
export default PostPriceScreen;
