import React, { useState, useEffect } from "react";
import {
  View,
  FlatList,
  TouchableWithoutFeedback,
  StyleSheet,
} from "react-native";
import { ListItemSeparator } from "../components/lists";
import ActivityIndicator from "../components/ActivityIndicator";
import Button from "../components/Button";
import colors from "../config/colors";
import routes from "../navigation/routes";
import Text from "../components/Text";
import useApi from "../hooks/useApi";
import srvapi from "../api/srvapi";
import { gregorianToJalali } from "../services/jalaliDate";
import { numAddCama } from "../services/numAddCama";
import defaultStyles from "../config/styles";
import Snackbar from "../components/Snackbar";

function MyOrderList({ navigation, route }) {
  const user = route.params;
  const [myOrders, setMyOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [errLoading, seterrLoading] = useState(false);
  const [isMounted, setIsMounted] = useState(true);
  const [snackbar, setSnackbar] = useState({
    msg: "",
    open: false,
    backColor: defaultStyles.colors.success,
  });

  useEffect(() => {
    return () => setIsMounted(false);
  }, []);

  // const getOrdersApi = useApi(() => {
  //   srvapi
  //     .get("/orders", { params: { type: "U", userid: user._id } })
  //     .catch((err) => {
  //       console.log(err);
  //     });
  // });
  const getOrdersApi = () => {
    setLoading(true);
    seterrLoading(false);
    const getOrders = async () => {
      const result = await srvapi
        .get("/orders", { params: { type: "U", userid: user._id } })
        .catch((err) => {
          if (isMounted) {
            seterrLoading(true);
            setLoading(false);
            setMyOrders([]);
            if (err?.errcode === 403) {
              setSnackbar({
                msg: "دسترسی شما منقضی شده لطفا مجدد وارد برنامه شوید",
                open: true,
                backColor: defaultStyles.colors.danger,
              });
            }
          }
        });
      if (isMounted) setMyOrders(result?.data ?? []);
    };
    getOrders();
    if (isMounted) setLoading(false);
  };
  const DateGtoJ = (pDate) => {
    const gDateAr = new Date(pDate).toISOString().substring(0, 10).split("-");
    return gregorianToJalali(gDateAr[0], gDateAr[1], gDateAr[2], true);
  };
  useEffect(() => {
    // getOrdersApi.request();
    getOrdersApi();
  }, []);

  return (
    <>
      <ActivityIndicator visible={loading} />
      <View style={styles.screen}>
        {errLoading && (
          <>
            <Text>خطا در بازیابی لیست سفارشات</Text>
            <Button
              title="تلاش مجدد / ورود مجدد به سیستم"
              onPress={getOrdersApi}
            />
          </>
        )}
        <FlatList
          data={myOrders}
          keyExtractor={(order) => order._id}
          ItemSeparatorComponent={() => (
            <ListItemSeparator backgroundColor={colors.medium} />
          )}
          renderItem={({ item }) => (
            <TouchableWithoutFeedback
              onPress={() => navigation.navigate(routes.ORDER_DETAILS, item)}
            >
              <View style={styles.card}>
                <Text style={styles.title} numberOfLines={1}>
                  {`تاریخ سفارش: ${DateGtoJ(item.updated)}`}
                </Text>
                <Text style={styles.title} numberOfLines={1}>
                  {item.banktx === ""
                    ? "وضعیت تراکنش نامشخص"
                    : item.delivery_status
                    ? "سفارش تحویل شده  "
                    : "سفارش در حال ارسال  "}
                </Text>
                <Text style={styles.title} numberOfLines={1}>
                  {` شماره پیگیری ${item.banktx}`}
                </Text>
                <Text style={styles.subTitle} numberOfLines={1}>
                  {`مبلغ پرداختی ${numAddCama(
                    item.totalPrice +
                      item.deliveryCost -
                      item.totalDiscount -
                      item.discountamount
                  )} تومان`}
                </Text>
                {~~item.discountamount > 0 && (
                  <Text style={styles.discount} numberOfLines={1}>
                    {`مبلغ کد تخفیف ${numAddCama(item.discountamount)} تومان`}
                  </Text>
                )}
              </View>
            </TouchableWithoutFeedback>
          )}
        />
        <Snackbar
          time={5000}
          open={snackbar.open}
          msg={snackbar.msg}
          backColor={snackbar.backColor}
          onPress={() => setSnackbar({ ...snackbar, open: false })}
        />
      </View>
    </>
  );
}

const styles = StyleSheet.create({
  screen: {
    padding: 20,
    backgroundColor: colors.light,
    flex: 1,
  },
  card: {
    borderRadius: 15,
    backgroundColor: colors.white,
    marginVertical: 10,
  },
  subTitle: {
    color: colors.black,
    fontWeight: "bold",
  },
  discount: {
    color: colors.danger,
    fontWeight: "bold",
  },
  title: {
    marginBottom: 7,
  },
});

export default MyOrderList;
