import React from "react";
import { View, Text, Switch, StyleSheet, Platform } from "react-native";

import defaultStyles from "../config/styles";

function Switchs({ width = "100%", switchTitle, value, onChangeSwitch }) {
  return (
    <View
      style={[styles.container, { width }, { flexDirection: "row-reverse" }]}
    >
      <Text style={styles.text}>{switchTitle}</Text>
      <Switch onValueChange={onChangeSwitch} value={value} />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: defaultStyles.colors.light,
    borderRadius: 25,
    flexDirection: "row",
    padding: 15,
    marginVertical: 10,
    alignItems: "center",
  },
  text: {
    padding: 3,
    fontSize: 16,
    fontFamily: Platform.OS === "android" ? "Roboto" : "Avenir",
  },
});

export default Switchs;
