import srvapi from "./srvapi";

const endpoint = "/product";

export const getProducts = (options) => {
  return srvapi.get("/products", { ...options }).catch((err) => {
    return Promise.reject(err);
  });
};

export const addProduct = (productInfo, onUploadProgress, options) => {
  const data = new FormData();
  data.append("_id", productInfo._id);
  data.append("prdname", productInfo.prdname);
  data.append("price", productInfo.price);
  data.append("discount", productInfo.discount ? productInfo.discount : "0");
  data.append("prdcategory", productInfo.prdcategory.value);
  data.append("remarks", productInfo.remarks);
  let filename, match, type;
  productInfo.images.forEach((image) => {
    filename = image.split("/").pop();
    match = /\.(\w+)$/.exec(filename);
    type = match ? `image/${match[1]}` : `image`;
    data.append("images", {
      name: filename,
      type: "image/jpeg",
      uri: image,
    });
  });

  return srvapi
    .post(endpoint, data, {
      ...options,
      onUploadProgress: (progress) =>
        onUploadProgress(progress.loaded / progress.total),
    })
    .catch((err) => {
      console.log("e1:", err);
    });
};
export const updProduct = (productInfo, onUploadProgress, options) => {
  return srvapi
    .put(
      endpoint,
      { ...productInfo, prdcategory: productInfo.prdcategory.value },
      {
        ...options,
        onUploadProgress: (progress) =>
          onUploadProgress(progress.loaded / progress.total),
      }
    )
    .catch((err) => {
      console.log("e3:", err);
    });
};

export default {
  addProduct,
  updProduct,
  getProducts,
};
